.home-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
  }
  
  .hero {
    padding: 30px 10px;
    background: linear-gradient(135deg, #150333 10%, #2a5298 100%);
    color: white;
    border-radius: 10px;
    margin-bottom: 10px;
    width: 100%;
    box-sizing: border-box;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
  }
  
  .description {
    font-size: 1.2rem;
    line-height: 1.6;
    max-width: 600px;
    margin: 0 auto 20px;
  }
  
  .button-group {
    display: flex;
    justify-content: center;
    gap: 15px;
  }
  
  .button {
    padding: 12px 24px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .button.primary {
    background-color: #007bff;
    color: white;
  }
  
  .button.primary:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  .button.secondary {
    background-color: transparent;
    color: white;
    border: 2px solid white;
  }
  
  .button.secondary:hover {
    background-color: rgba(255, 255, 255, 0.2);
    transform: scale(1.05);
  }
  
  .networks {
    padding: 20px;
  }
  
  .networks h2 {
    font-size: 1.8rem;
    margin-bottom: 15px;
    color: var(--text);
    text-transform: uppercase;
  }
  
  .network-list {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .network-list li {
    font-size: 1rem;
    color: var(--text);
  }
  
  .network-note {
    font-size: 0.9rem;
    color: var(--text);
    max-width: 600px;
    margin: 0 auto;
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .hero h1 {
      font-size: 2rem;
    }
  
    .description {
      font-size: 1rem;
    }
  
    .button-group {
      flex-direction: column;
      gap: 10px;
    }
  
    .button {
      width: 100%;
      max-width: 300px;
      margin: 0 auto;
    }
  }
  .social-links {
    padding: 20px;
  }
  
  .social-links {
    padding: 20px;
  }
  
  .social-links h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    color: var(--text, #333);
  }
  
  .social-icons {
    display: flex;
    justify-content: center;
    gap: 15px;
    flex-wrap: wrap;
  }
  
  .social-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 1.5rem;
    color: white;
    text-decoration: none;
    transition: transform 0.2s, opacity 0.2s;
  }
  
  .social-icon:hover {
    transform: scale(1.1);
    opacity: 0.9;
  }
  
  .x-icon {
    background-color: #1DA1F2; /* Color de X/Twitter */
  }
  
  .telegram-icon {
    background-color: #0088cc; /* Color de Telegram */
  }
  
  .discord-icon {
    background-color: #7289da; /* Color de Discord */
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .social-icons {
      gap: 10px;
    }
  
    .social-icon {
      width: 36px;
      height: 36px;
      font-size: 1.3rem;
    }
  }